body {
  direction: rtl;
  text-align: right !important;
  font-family: "Cairo", sans-serif, "Arial", "Verdana" !important;
}

.navbar-nav {
  padding-right: 0;
}

.nav-links-right {
  margin-right: auto !important;
  margin-left: 0 !important;
}

.list-group {
  padding-right: 0;
}

.small-box .icon > i {
  left: 15px;
  right: auto;
}

.layout-fixed .main-sidebar {
  right: 0;
  left: auto;
}

.sidebar-collapse .main-sidebar,
.sidebar-collapse .main-sidebar:before {
  margin-right: -250px;
  margin-left: auto;
}

.main-sidebar,
.main-sidebar:before {
  transition: margin-right 0.3s ease-in-out, width 0.3s ease-in-out;
}

@media (min-width: 992px) {
  .sidebar-mini.sidebar-collapse .content-wrapper,
  .sidebar-mini.sidebar-collapse .main-footer,
  .sidebar-mini.sidebar-collapse .main-header {
    margin-right: 4.6rem !important;
    margin-left: auto !important;
  }

  .sidebar-mini.sidebar-collapse .main-sidebar,
  .sidebar-mini.sidebar-collapse .main-sidebar:before {
    margin-right: 0;
    margin-left: auto;
    width: 4.6rem;
  }
}

@media (min-width: 576px) {
  .content-wrapper,
  .main-footer,
  .main-header {
    transition: margin-right 0.3s ease-in-out;
    margin-right: 0;
    margin-left: 0;
  }

  .sidebar-collapse .content-wrapper,
  .sidebar-collapse .main-footer,
  .sidebar-collapse .main-header {
    margin-right: 0;
    margin-left: auto;
  }
}

@media (max-width: 767.98px) {
  .main-sidebar,
  .main-sidebar:before {
    margin-right: -250px;
    margin-left: 0;
  }

  .content-wrapper,
  .content-wrapper:before,
  .main-footer,
  .main-footer:before,
  .main-header,
  .main-header:before {
    margin-left: 0;
    margin-right: 0;
  }

  .sidebar-open .main-sidebar,
  .sidebar-open .main-sidebar:before {
    margin-right: 0;
  }
}

.nav {
  padding-right: 0;
}

.sidebar .nav-link p,
.main-sidebar .brand-text,
.sidebar .user-panel .info {
  transition: margin-right 0.3s linear;
}

.nav-sidebar .nav-link > p > .right {
  right: auto;
  left: 1rem;
}

.nav-sidebar > .nav-item .nav-icon {
  margin-left: 0.2rem;
  margin-right: auto;
}

.nav-sidebar .menu-open > .nav-link i.right {
  transform: rotate(90deg);
}

.btn.maximize-table {
  float: left;
}

.box-profile .list-group-item a {
  float: left !important;
}

#modal_location_hotel .modal-header button.close {
  margin-left: 0 !important;
  margin-right: 0 !important;
}

.profile-wrapper .offers-in-profile .offer .list-group .list-group-item > a {
  float: left !important;
}

.custom-control.custom-checkbox {
  right: 5px;
  padding-left: 0;
  padding-right: 1.5rem;
}

.custom-control.custom-checkbox .custom-control-label:before,
.custom-control.custom-checkbox .custom-control-label:after {
  right: -1.5rem;
  left: auto;
}

.col-details.plus label {
  position: relative;
  right: -17px;
  left: auto;
}

.form-add-sales-in-sweetalert {
  padding: 20px 60px;
}

